<template>
  <div class="referer">
    <list
      v-model="loading"
      :finished="finished"
      :error.sync="error"
      @load="getReferer"
    >
      <template v-for="(item, index) in referers">
        <div
          :key="index"
          class="referer-box lm-1px-b"
          @click="jump(item)"
        >
          <clz-header :clz="item" />
          <div class="referer-bottom">
            <div style="text-align:start;">
              <span class="content-font">招生&emsp;</span>
              <span class="referer-bottom-content">{{ item.RecruitStudentsCount }}</span>
            </div>
            <div class="lm-1px-l lm-1px-r">
              <span class="content-font">推广人数&emsp;</span>
              <span class="referer-bottom-content">{{ item.SpreadCount }}</span>
            </div>
            <div style="text-align:end;">
              <span class="content-font">推广招生&emsp;</span>
              <span class="referer-bottom-content">{{ item.SpreadStudentCount }}</span>
            </div>
          </div>
        </div>
      </template>
    </list>
  </div>
</template>

<script>
import createList from '@/utils/create/create-list'
import ClzHeader from '@/views/classroom/detail/components/clz-header'
export default createList({
  components: { ClzHeader },
  data () {
    return {
      referers: []
    }
  },
  mounted () {
    this.getReferer()
  },
  methods: {
    getReferer () {
      // this.loading = true
      this.$store
        .dispatch('getMyRefererClassList', {
          ...this.page()
        })
        .then(({ data, total }) => {
          this.referers = this.referers.concat(data)
          this.loading = false
          this.total = this.referers.length
          this.complateLoad(total)
        })
        .catch(() => {
          this.errorLoad()
        })
    },
    jump (item) {
      this.$router.push({
        name: 'RefererDetail',
        params: { item }
      })
    }
  }
})
</script>

<style lang="less" scoped>
.referer {
  background-color: @white;
  &-box {
    border-radius: 5px;
    padding: 0 20px 20px;
  }
  &-bottom {
    padding: 0  20px ;
    display: flex;
    align-items: center;
    & > div {
      flex: 1;
      text-align: center;
    }
    &-content {
      color: @blue;
      font-size: 24px;
    }
  }
}
</style>
